					能危及到员工暖心购账户资金安全情形时，应立即以有效方式通知本公司，向本公司申请暂停相关服务，以保障您的合法权益；及（b）确保您在持续登录员工暖心购网站时段结束时，以正确步骤离开网站。</span><span
					style="font-family:微软雅黑; font-size:10.5pt; font-weight:bold; text-decoration:underline">本公司不能也不会对因您未能遵守本款约定而发生的任何损失、损毁及其他不利后果负责。您理解本公司对您的请求采取行动需要合理期限，在此之前，本公司对已执行的指令及（或）所导致的您的损失不承担任何责任。</span>
