					style="font-family:微软雅黑; font-size:10.5pt; font-weight:bold; text-decoration:underline">4、除非您本人书面提出申请或者另有法律规定或经司法裁判，且征得本公司同意，否则您的员工暖心购登录名及密码、员工暖心购账户不得以任何方式转让、赠与或继承（相关的财产权益除外）。</span>
