					style="font-family:微软雅黑; font-size:10.5pt">7、本公司有权了解您使用本公司产品或服务的真实交易背景及目的，您应如实提供本公司所需的真实、全面、准确的信息。</span><span
					style="font-family:微软雅黑; font-size:10.5pt; font-weight:bold; text-decoration:underline">如果本公司有合理理由怀疑您提供虚假交易信息的，本公司有权暂时或永久限制您所使用的产品或服务的部分或全部功能，并通过邮件或者站内信或客户端通知等方式通知您，您应及时予以关注。</span>
