					style="font-family:微软雅黑; font-size:10.5pt">8、您同意，本公司有权按照包括但不限于公安机关、检察机关、法院、海关、税务机关等司法机关、行政机关、军事机关的要求对您的个人信息及在员工暖心购的交易及账户等进行查询、冻结或其他操作。</span>
