			<p style="font-size:10.5pt; line-height:115%; margin:0pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt; font-weight:bold">（三）注销相关</span></p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt">在需要终止使用本服务时，您可以申请注销您的员工暖心购账户，您同意：</span></p>
