					style="font-family:微软雅黑; font-size:10.5pt">1、您所申请注销的员工暖心购账户应当是您依照本协议的约定注册并由本公司提供给您本人的账户。您应当依照本公司规定的程序进行员工暖心购账户注销。</span>
