			<p style="font-size:10.5pt; line-height:115%; margin:0pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt">2、员工暖心购账户注销将导致本公司终止为您提供本服务，本协议约定的双方的权利义务终止（依本协议其他条款另行约定不得终止的或依其性质不能终止的除外），同时还可能对于该账户产生如下结果：</span>
