					style="font-family:微软雅黑; font-size:10.5pt">A、任何您的交易信息都将无法查看；</span></p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt">B、任何您的交易过程中产生的附属文档都将无法查看，您可以在注销之前进行保存。</span></p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt">C、注销完成后，本公司无法对您之前留存下来的任何信息提供鉴权等服务</span></p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt">3、您可以通过自助或者人工的方式申请注销员工暖心购账户。</span></p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt">4、您申请注销的员工暖心购账户应当处于正常状态，即您的员工暖心购账户的账户信息和用户信息是最新、完整、正确的，且该账户可以使用所有员工暖心购服务功能。账户信息或用户信息过时、缺失、不正确的账户或被暂停或终止提供服务的账户不能被申请注销。如您申请注销的账户有关联账户或子账户的，在该关联账户或子账户被注销前，该账户不得被注销。</span>
			</p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt">5、您申请注销的员工暖心购账户应当不存在任何由于该账户被注销而导致的未了结的合同关系与其他基于该账户的存在而产生或维持的权利义务，及本公司认为注销该账户会由此产生未了结的权利义务而产生纠纷的情况。</span>
