					style="font-family:微软雅黑; font-size:10.5pt; font-weight:bold; text-decoration:underline">7、您理解并同意，如（a）您连续12个月未使用您的员工暖心购登录名；或（b）您在员工暖心购网站有欺诈、虚假交易、侵犯他人合法权益或其他严重违反员工暖心购网站规则或者中华人民共和国法律的行为的，本公司有权注销您名下的全部或部分员工暖心购登录名，您将不能再登录员工暖心购网站，所有网站的服务将同时终止。</span>
