					style="font-family:微软雅黑; font-size:13.5pt">五、 员工暖心购服务使用规则</span></p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt">为有效保障您使用本服务时的合法权益，您理解并同意接受以下规则：</span></p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt">（一）一旦您使用本服务，您即不可撤销地授权本公司在您及（或）您指定人符合指定条件或状态时，向您的合作伙伴发送签约邀请并执行对应的操作。</span>
