					style="font-family:微软雅黑; font-size:10.5pt">A、您在本网站或其他可使用本服务的网站或软件上通过以您的员工暖心购账户名及密码或数字证书等安全产品登录员工暖心购账户并依照本服务预设流程所修改或确认的交易状态或指令；B、您通过您注册时作为该账户名称或者与该账户绑定的手机或其他专属于您的通讯工具（以下合称
					该手机）号码向本公司发送的信息（短信或电话等）回复；C、您通过您注册时作为该账户名称或者与该账户名称绑定的其他硬件、终端、软件、代号、编码、代
					码、其他账户名等有形体或无形体向本公司发送的信息（如本方式所指有形体或无形体具备与该手机接受信息相同或类似的功能，以下第五条第（四）、（五）、
					（六）项和第六条第（三）项涉及该手机的条款同样适用于本方式）；D、通过识别您的指纹等生物特征；E、本公司与您约定或本公司认可的其他方式。</span><span
					style="font-family:微软雅黑; font-size:10.5pt; font-weight:bold; text-decoration:underline">无论您通过以上何种方式向本公司发出指令，都不可撤回或撤销，且成为本公司代理您支付或收取款项或进行其他操作的唯一指令，视为您本人的指令，您应当自己对本公司忠实执行上述指令产生的任何结果承担责任。</span><span
					style="font-family:微软雅黑; font-size:10.5pt">本协议所称绑定，指您的员工暖心购账户与本条上述所称有形体或无形体存在对应的关联关系，这种关联关系使得员工暖心购服务的某些服务功能得以实现，且这种关联关系有时使得这些有形体或无形体能够作为本公司对您的员工暖心购账户的识别和认定依据。</span>
