					Authority），由CA以出具数字证书之目的使用并保存，且前述信息至少保存至数字证书失效后或相关电子数据（包括但不限于签约合同电子数据）产生后5年，并且该等信息不因您终止员工暖心购服务而停止保存。同时，本公司有权将您的个人身份信息、证件复印件或扫描件相应传输至征信机构及其他具有国家认可资质的数据服务提供商，由该等机构以验证签约方身份之目的使用并保存。</span>
