					style="font-family:微软雅黑; font-size:10.5pt; font-weight:bold; text-decoration:underline">1、在使用本服务时，若您或您的签约对方未遵从本服务条款或网站说明、交易页面中之操作提示、规则），则本公司有权拒绝为您与签约对方提供相关服务，且本公司不承担损害赔偿责任。</span>
