					style="font-family:微软雅黑; font-size:10.5pt; font-weight:bold; text-decoration:underline">1、在您使用本服务时，本公司有权依照《员工暖心购服务收费规则》向您收取服务费用。本公司拥有制订及调整服务费之权利，具体服务费用以您使用本服务时本网站或客户端、产品页面上所列之收费方式公告或您与本公司达成的其他书面协议为准。</span>
