					style="font-family:微软雅黑; font-size:10.5pt">（十三）您认可员工暖心购账户的使用记录、交易状态等数据等均以本公司系统记录的数据为准。如您对该等数据存有异议的，应自您账户数据发生变动之日起三日内向本公司提出异议，并提供相关证据供本公司核实。</span>
